var maps = {};
import * as d3scale from 'd3-scale';
var cs = ['#381f41', '#664273','#a79cab','#41624b'];
var ts = d3scale.scaleThreshold().domain([-25,-0.1,0.1]).range(['groer Rckgang','Rckgang','keine Vernderung','Zuwachs']);
maps['kindergaerten'] = {
  title: 'Wo Eltern ihre Kinder abgemeldet haben',
  description: 'In dieser Karte wird die prozentuelle Vernderung der Anzahl der Kindergartenkinder in Nachmittagsbetreuung zwischen Jnner und Mrz/April 2018 dargestellt. Gemeinden, die keine Auskunft gegeben oder keine Nachmittagsbetreuung haben werden gestreift dargestellt.',
  detail: 'In einigen Gemeinden gibt es <span class="scalevalue">keine Betreuung/Kooperation</span> oder nur eine Betreuung durch <span class="scalevalue">Caritas oder andere private Erhalter</span>, manche haben <span class="scalevalue">keine Auskunft</span> gegeben.',
  data: 'gemeinden.csv',
  data_key: 'gkz',
  property_key: 'GKZ',
  source: 'Eigenrecherche',
  is_gemeinden: false,
  search_keys: ['PG'],
  search_title: 'Gemeindesuche',
  topojson: 'gemeinden.topojson',
  value: function(d) {
    if(d.length==0) {
      return null;
    }
    d = d[0];
    if(d.Kategorie=='Alles da') {
      return [0,ts(d['Prozentuelle nderung'])];
    }
    return [1,d.Kategorie];
  },

  scale: 'threshold-or-category',

  search: true,
  colorschemes: [
    cs,
    ['#888888','#000000', '#820B0B']
  ],
  legend: 0,
  thresholds: {colors: cs, thresholds: ts.domain(), labels: ts.range()},

  categories: [['groer Rckgang', 'Rckgang', 'keine Vernderung', 'Zuwachs'],
    ['keine Betreuung/Kooperation', 'Caritas oder anderer privater Erhalter', 'keine Auskunft']],
  /*baselayer: () => L.tileLayer('https://{s}.wien.gv.at/basemap/bmaporthofoto30cm/normal/google3857/{z}/{y}/{x}.jpeg', {
      subdomains: ["maps", "maps1", "maps2", "maps3"],
      attribution: 'cc-by 3.0 basemap.at',
      detectRetina: true
  }) ,*/

  tooltip: function(d, p, pctfmt, numfmt) {
    if(d.length==0) {
      return p.PG;
    }
    d = d[0];
    return `<strong>${d.name}</strong>`+
      (d.Kategorie=='Alles da'?`
      <span style="padding-left: 0.5em; font-weight: bold; font-size: 1.25em; float: right; display: inline-block;">${d['Prozentuelle nderung']>0?'+':''}${pctfmt(d['Prozentuelle nderung'])}%</span>
<br />
Kindergartenkinder: ${numfmt(d['Kinder insgesamt'])}<br />
Kinder am Nachmittag vor Gebhr: ${numfmt(d['Jnner: Kinder in Nachmittagsbetreuung'])}<br />
Kinder am Nachmittag nach Gebhr: ${numfmt(d['Mrz: Kinder in Nachmittagsbetreuung'])}<br />
Vernderung: ${d.Differenz>0?'+':''}${numfmt(d['Differenz'])}
        `:(
          d.Kategorie=='keine Auskunft'?`<br />
          Mitarbeiter der Gemeinde ${d.name} haben das Beantworten unserer Fragen mehrfach abgelehnt.
          Womglich haben Sie mehr Glck: Rufen die Gemeinde unter ${d.Telefonnummer} an und teilen Sie uns die
          Rckmeldung ber <a target="_blank" href="https://form.jotformeu.com/81265006658357">dieses Formular</a> mit.
`:`<br />${d.Kategorie}`))+ `
    ${d['Karte-Text']?'<br /><span class="kartetext">'+d['Karte-Text']+'</span>':''}`;
  }
};

Object.keys(maps).map((x) => {maps[x].map=x});

export { maps };



